import { dataExplorerStore } from './store/dataExplorer';
export function textFormat(text, substitutions) {
    Object.keys(substitutions).forEach(function (subkey) {
        text = text.split("{" + subkey + "}").join(substitutions[subkey].toString());
    });
    return text.toString();
}
export function numberFormat(number, units) {
    var languageCode = dataExplorerStore.getState().languageCode;
    var formattedNumber = new Intl.NumberFormat(languageCode, { minimumFractionDigits: 1, maximumFractionDigits: 1 }).format(number);
    if (!units)
        return formattedNumber;
    var spacing = true;
    if (languageCode == "en-ca" && units == "%")
        spacing = false;
    return "" + formattedNumber + (spacing ? " " : "") + units;
}
