import * as d3 from "d3";
import { numberFormat } from "./translator";
var marginY = 10;
var marginX = 60;
var width = 700;
var height = 400;
var moveToFront = function (node) {
    return node.each(function () {
        node.parentNode.appendChild(node);
    });
};
var moveToBack = function (node) {
    return node.each(function () {
        var firstChild = node.parentNode.firstChild;
        if (firstChild) {
            node.parentNode.insertBefore(node, firstChild);
        }
    });
};
var isBetween = function (val, up, low) { return val >= low && val <= up; };
var isPointInRange = function (upper, lower, point) {
    if (!(upper && lower))
        return false;
    var pUpper = point.valueUpper;
    var pLower = point.valueLower;
    var pVal = point.value;
    if (!(pUpper && pLower && pVal))
        return false;
    // check if the selected bound is within the points bounds
    if (isBetween(upper, pUpper, pLower))
        return true;
    if (isBetween(lower, pUpper, pLower))
        return true;
    // check if point upper and lower bound is within selected bounds
    if (isBetween(pVal, upper, lower))
        return true;
    if (isBetween(pLower, upper, lower))
        return true;
    if (isBetween(pUpper, upper, lower))
        return true;
    return false;
};
function wrap(text, width, inverted) {
    if (inverted === void 0) { inverted = false; }
    if (Object.keys(window).length == 0)
        return;
    text.each(function () {
        var text = d3.select(this), words = text.text().split(/\s+/).reverse(), word, line = [], lineNumber = 0, lineHeight = 1.1, // ems
        x = text.attr("x"), y = text.attr("y"), dy = 0, //parseFloat(text.attr("dy")),
        tspan = text.text(null)
            .append("tspan")
            .attr("x", x)
            .attr("y", y)
            .attr("dy", -dy + "em");
        while (word = words.pop()) {
            line.push(word);
            tspan.text(line.join(" "));
            if (tspan.node().getComputedTextLength() > width) {
                line.pop();
                tspan.text(line.join(" "));
                line = [word];
                tspan = text.append("tspan")
                    .attr("x", x || 0)
                    .attr("y", y)
                    .attr("dy", (inverted ? -1 : 1) * (++lineNumber * lineHeight + dy) + "em")
                    .text(word);
            }
        }
    });
}
export function renderChart(ref, dataset, animate, highlightIndex, highlightUpper, highlightLower, isTrend, updateHighlight) {
    var chart = d3.select(ref);
    var select = chart.select(".main");
    var points = dataset.points.filter(function (point) { return point.type == 0 || isTrend; });
    var averages = dataset.points.filter(function (point) { return point.type != 0 && !isTrend; });
    var animationDuration = 600;
    d3.selection.prototype.optionalTransition = function () {
        if (!animate)
            return this;
        return this.transition().duration(animationDuration);
    };
    if (points.length == 0) {
        points = averages;
        averages = [];
    }
    var x = d3.scaleBand()
        .domain(points.map(function (point) { return point.label; }))
        .range([0, width]);
    var y = d3.scaleLinear()
        .domain([0, d3.max(dataset.points, function (point) { return (point.valueUpper || point.value || 0) * 1.1; })])
        .range([height, 0]);
    chart.selectAll("g.y-axis")
        .attr("transform", "translate(" + marginX + ", " + marginY + ")")
        // @ts-ignore
        .optionalTransition()
        // @ts-ignore
        .call(d3.axisLeft(y))
        .selectAll("text")
        .attr("font-size", "10px");
    chart.selectAll("g.x-axis")
        .attr("transform", "translate(" + marginX + ", " + (height + marginY) + ")")
        // @ts-ignore
        .call(d3.axisBottom(x))
        .selectAll(".tick text")
        .attr("font-size", "10px")
        .attr("y", 15)
        .attr("text-anchor", "middle")
        .call(wrap, 500 / points.length);
    chart.select('.xAxisLabel')
        .text(dataset.xAxis)
        .call(wrap, 600);
    chart.select('.yAxisLabel')
        .text(dataset.yAxis)
        .call(wrap, 400);
    var pointBinding = select.selectAll('g.point').data(points);
    var averageBinding = select.selectAll('g.average').data(averages);
    var cvUpperBinding = select.selectAll('g.cvUpper').data(points);
    var cvLowerBinding = select.selectAll('g.cvLower').data(points);
    var cvConnectBinding = select.selectAll('g.cvConnect').data(points);
    var enteredcvUpper = pointBinding.enter().append("g").attr("class", "cvUpper").append("rect");
    var enteredRect = pointBinding.enter().append("g").attr("class", "point").append("rect");
    enteredRect.attr("x", function (d, i) { return (i + 0.5) * (width / points.length) - (isTrend ? 10 : 25) / 2; })
        .attr("width", isTrend ? 10 : 25)
        //.style("fill", "steelblue")
        .attr("ry", (isTrend ? 10 : 0))
        .attr("rx", (isTrend ? 10 : 0))
        .attr("y", height)
        // @ts-ignore
        .optionalTransition()
        .attr("y", function (d) { return isTrend ? y(d.value || 0) - 5 : y(d.value || 0); })
        .attr("height", function (d) { return isTrend ? 10 : height - y(d.value || 0); })
        .attr("fill", "steelblue")
        .attr("opacity", function (d, i) { return isPointInRange(highlightUpper, highlightLower, d) && i != highlightIndex ? 0.2 : 1; });
    enteredRect.append("title")
        .text(function (d) { return d.label + ": " + numberFormat(d.value || 0, dataset.unit); });
    pointBinding
        .select("rect")
        // @ts-ignore
        .optionalTransition()
        .attr("ry", (isTrend ? 10 : 0))
        .attr("rx", (isTrend ? 10 : 0))
        .attr("width", isTrend ? 10 : 25)
        .attr("x", function (d, i) { return (i + 0.5) * (width / points.length) - (isTrend ? 10 : 25) / 2; })
        .attr("height", function (d, i) { return isTrend ? 10 : height - y(d.value || 0); })
        .attr("y", function (d) { return isTrend ? y(d.value || 0) - 5 : y(d.value || 0); })
        .attr("fill", "steelblue")
        .attr("opacity", function (d, i) { return isPointInRange(highlightUpper, highlightLower, d) && i != highlightIndex ? 1 : 1; }) //Point A to 0.2 to bring back functionality
        .select("title")
        .text(function (d) { return d.label + ": " + numberFormat(d.value || 0, dataset.unit); });
    pointBinding.on("mouseover", function (_, i) { return updateHighlight(i); });
    pointBinding.on("mouseout", function (_, i) { return updateHighlight(-1); });
    pointBinding.exit().remove();
    enteredcvUpper.attr("x", function (d, i) { return (i + 0.5) * (width / points.length) - 25 / 2; })
        .attr("width", 25)
        .style("fill", "black")
        // .attr("y", height)
        // @ts-ignore
        .optionalTransition()
        .attr("y", function (d) { return y(d.valueUpper || 0); })
        .attr("height", 2);
    cvUpperBinding
        .select("rect")
        // @ts-ignore
        .optionalTransition()
        .attr("width", 25)
        .attr("x", function (d, i) { return (i + 0.5) * (width / points.length) - 25 / 2; })
        .attr("height", 2)
        .attr("y", function (d) { return y(d.valueUpper || 0); })
        .style("fill", "black");
    cvUpperBinding.exit().remove();
    var enteredcvConnect = cvConnectBinding.enter().append("g").attr("class", "cvConnect").append("rect");
    enteredcvConnect.attr("x", function (d, i) { return (i + 0.5) * (width / points.length) - 2 / 2; })
        .attr("width", 2)
        .style("fill", "black")
        .attr("y", height)
        // @ts-ignore
        .optionalTransition()
        .attr("height", function (d) { return y(d.valueLower || 0) - y(d.valueUpper || 0); })
        .attr("y", function (d) { return y(d.valueUpper || 0); });
    cvConnectBinding
        .select("rect")
        // @ts-ignore
        .optionalTransition()
        .attr("width", 2)
        .attr("x", function (d, i) { return (i + 0.5) * (width / points.length) - 2 / 2; })
        .attr("height", function (d) { return y(d.valueLower || 0) - y(d.valueUpper || 0); })
        .attr("y", function (d) { return y(d.valueUpper || 0); })
        .style("fill", "black");
    cvConnectBinding.exit().remove();
    var enteredcvLower = pointBinding.enter().append("g").attr("class", "cvLower").append("rect");
    enteredcvLower.attr("x", function (d, i) { return (i + 0.5) * (width / points.length) - 25 / 2; })
        .attr("width", 25)
        .style("fill", "black")
        .attr("y", height)
        // @ts-ignore
        .optionalTransition()
        .attr("y", function (d) { return y(d.valueLower || 0); })
        .attr("height", 2);
    cvLowerBinding
        .select("rect")
        // @ts-ignore
        .optionalTransition()
        .attr("width", 25)
        .attr("x", function (d, i) { return (i + 0.5) * (width / points.length) - 25 / 2; })
        .attr("height", 2)
        .attr("y", function (d) { return y(d.valueLower || 0); })
        .style("fill", "black");
    cvLowerBinding.exit().remove();
    var enteredAverage = averageBinding.enter().append("g");
    enteredAverage
        .attr("class", "average")
        .append("rect")
        .attr('fill', 'url(#linePattern)')
        .attr("height", 2)
        .attr("x", 0)
        .attr("width", width)
        .attr("ry", (isTrend ? 10 : 0))
        .attr("rx", (isTrend ? 10 : 0))
        // .attr("y", height)
        // @ts-ignore
        .optionalTransition()
        .attr("y", function (d) { return y(d.value || 0); });
    var lowestValueIndex = (points.map(function (p) { return +p.value; }).indexOf(Math.min.apply(Math, points.map(function (p) { return +p.value; }))));
    if (points.length == 2)
        lowestValueIndex = 1.5;
    var leftLabel = lowestValueIndex < points.length / 2;
    enteredAverage
        .append("text")
        .attr("x", function () { return width; })
        .attr("y", height)
        .attr("filter", "url(#solid)")
        // @ts-ignore
        .optionalTransition()
        .attr("y", function (d) { return y(d.value || 0) - 5; })
        .text(function (d) { return d.label + ": " + numberFormat(d.value || 0, dataset.unit); })
        .attr("text-anchor", "end")
        .style("font-weight", "bold");
    averageBinding
        .select("rect")
        // @ts-ignore
        .optionalTransition()
        .attr("y", function (d) { return y(d.value || 0); });
    averageBinding
        .select("text")
        .attr("filter", "url(#solid)")
        // @ts-ignore
        .optionalTransition()
        .attr("x", function () { return width; })
        .attr("y", function (d) { return y(d.value || 0) - 5; })
        .text(function (d) { return d.label + ": " + numberFormat(d.value || 0, dataset.unit); })
        .attr("text-anchor", "end")
        .style("font-weight", "bold");
    averageBinding.exit()
        .selectAll("rect, text")
        // @ts-ignore
        .optionalTransition()
        .attr("y", -marginY)
        .style("opacity", 0);
    averageBinding.exit()
        // @ts-ignore    
        .optionalTransition()
        .remove();
    averageBinding.raise();
}
